.image-of-bo {
  background-image: url(/images/imageOfBo1.png);
  background-position: center;
  height: 75vh;
  background-repeat: no-repeat;
  margin-bottom: -50px;

  margin-left: -20px;
  margin-right: -20px;

  @include onTablet {
    margin-left: -72px;
    margin-right: -72px;
    background-image: url(/images/imageOfBo2.png);
  }

  @include onDesktop {
    margin-left: -120px;
    margin-right: -120px;
    background-image: url(/images/imageOfBo.png);
  }
}
